import React, { useEffect, useState } from "react";
import { useHistory , useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
function MyAlert(){
  return (
      <div className="my-alert">
        <p>재고가 얼마 남지 않았습니다.</p>
      </div>
  )
}
function Detail(props) {
    let history = useHistory();
    let { id } = useParams();
    let titleBox = styled.div`
      padding-top: 30px;
    `
    let Title = styled.h4`
      font-size : 25px;
      color : ${props => props.color}
    `
    let selectedData = props.guitar.find((i)=>{
        return i.id == id;
    })
    let [alertFlag,alertFlagChange] = useState(true);
    useEffect(()=>{
      //2초 후에 저거 alert 창을 안보이게하자
      let timer = setTimeout(()=>{alertFlagChange(!alertFlag)},2000)

      return()=>{clearTimeout(timer)}
    },[])
    let [input,inputChange] = useState('')
  return (
    <div className="container">
      <titleBox><Title className='red'>Detail</Title></titleBox>
      
      <input onChange={(v)=>inputChange(v)}/>


      {
        alertFlag
        ?<MyAlert></MyAlert>
        :null
      }
      
      
      <div className="row">
        <div className="col-md-6">
          <img
            src={selectedData.image}
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
  <h4 className="pt-5">{selectedData.title}</h4>
          <p>{selectedData.content}</p>
          <p>{selectedData.price} 원</p>
          <Info 재고={props.재고[selectedData.id]}/>
          <button className="btn btn-danger">주문하기</button>
          <button className="btn btn-danger" onClick={()=>{history.goBack()}}>뒤로가기</button>
        </div>
      </div>
    </div>
  );
}

function Info(props){
  return(
  <p>재고 : {props.재고}</p>
  )
}

export default Detail;